:root {
  --primary-color: #2c3e50; /* Midnight Blue */
  --secondary-color: #3498db; /* Dodger Blue */
  --button-bg-color: #27ae60; /* Nephritis Green */
  --button-delete-bg-color: #e74c3c; /* Alizarin Red */
  --text-color: #ecf0f1; /* Clouds */
  --font-family: 'Roboto', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--primary-color);
  color: var(--text-color);
  font-family: var(--font-family);
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  max-width: 500px;
  margin: 100px auto;
  height: fit-content;
  margin: 120px auto;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
  background-color: #34495e;
}

.logo {
  width: 80px;
  height: auto;
  margin-right: 20px;
}

h1{
  text-align: center;
  font-size: 36px;
  margin-bottom: 20px;
}

.form{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 12px
}

.form input[type="text"] {
  flex: 1;
  padding:10px;
  width: 85%;
  border: none;
  border-radius: 6px;
  outline: none;
}

.form input[type="text"]:focus,
.form button[type="submit"] {
  background-color: var(--button-bg-color);
  color: var(--text-color);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.form button[type="submit"]:hover {
  background-color: #2ecc71;
}

.todo-listItems {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.todo-item{
  display:flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding: 12px;
  border-radius: 8px;
  background-color: #2c3e50;
}

.item-content {
  width: 60%
}

.todo-item button {
  color: var(--text-color);
  border: none;
  border-radius: 6px;
  padding: 8px 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-right: 8px;
}

.todo-item button:hover {
  background-color: var(--button-bg-color);
}

.update-item{
  background-color: rgb(133, 133, 5);
}

.delete-item{
  background-color: var(--button-delete-bg-color);
}

.update-form {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.update-new-input {
  width: 80%;
  border-radius: 6px;
  border: 1px solid #eee;
  padding: 8px;
  outline: none;
}

.update-new-btn {
  background-color: rgb(34, 7, 123);
  color: #fff;
  cursor: pointer;
}

.completed-task {
  text-decoration: line-through;
}

.complete-button {
  background-color: #2980b9;
}

.search-icon-img {
  width: 50px;
  height: 30px;
  margin-left: 440px;
}